
.mask-layer2 {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .6);
    z-index: 99999;
}

.video-box2 {
    position: relative;
    width: 50%;
    height:fit-content;
    background: #FFFFFF;
    display: flex;
    justify-content:center;
    border-radius: 8px;
}

.video-box2>video {
    position: absolute;
    object-fit: fill;
    width: 96%;
    height: 85%;
    bottom: 3.3%;
}


.close-box2 {
    position: absolute;
    top: 6%;
    right: 3%;
    transform: translate(50%, -50%);
    width: 4%;
    height: 4%;
}

.close-box2>img {
    width: 100%;
    height: 100%;
    cursor: pointer;
}




@media screen and (max-width:769px) {
    .video-box2 {
        position: relative;
        width: 90%;
        height: 38%;
    }
}