.divbackground4 {
    width: 100%;
    height: 100%;
    background: #5d5d5c;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    z-index: 101;
}

.divQRcodeContainer4 {
    width: 5.0rem;
    height:5.0rem;
    background: #ffffff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
    border-radius: 0.25rem;
    z-index: 101;
}

.imgclose4{
    width: 0.44rem;
    height: 0.44rem;
    position: absolute;
    top: 3%;
    right: 3%;
}


.imgQRcode4 {
    WIDTH: 3.0rem;
    HEIGHT: 3.0rem;
    position: relative;
    top: 40%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
}

.divcompanyandcontact4 {
    WIDTH: 3.5rem;
    position: absolute;
    top: 75%;
    left: 0.6rem;
    color: #333333;
}

.divcompanyandcontact4 div{
    WIDTH: 5.0rem;
    font-size: 0.24rem;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #333333;
    line-height: 0.3rem;
    margin: 0.14rem;
    word-break:break-all
}

.divcompanyandcontact4 div img{ 
    width:0.24rem;
    height: 0.24rem;
    margin-right: 0.1rem;
    vertical-align:middle;

}


@media (max-width:350px){
    .divcompanyandcontact4 {
        left: 0.2rem;
    }


}