.icons-container2 {
    position: absolute;
    width: -moz-fit-content;
    width: 100%;
    height: 100px;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0) 100%);
    display: flex;
    justify-content: center;
}

.icon-each2 {
    position: relative;
    width: 80px;
    height: 80px;
    background: #ffffff;
    border-radius: 50%;
    float: left;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 10px;

}

 .icon-icon2 {
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    background-color: #ffffff;
    background-size: contain;
    cursor: pointer;
}

