.icon-container {
    position: absolute;
    width: fit-content;
    height: fit-content;
    /* opacity: 0.4; */
    border-radius: 20px;
    margin: 0 auto;
}

.icon-each {
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    float: left;
    top: 50%;
    /* transform: translateY(-50%); */
    margin-left: 43px;
    cursor: pointer;
    
}

.icon-each-img {
    position: relative;
    width: 38px;
	height: 38px;
    list-style:none;
    /* box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.5); */
}
