.divcolorchoose {
    position: absolute;
    /* top: 0; */
    right:  15px;
    width: fit-content;
    height: 40px;
    background: #d3d6da;
    /* opacity: 0.4; */
    border-radius: 20px;
    margin: auto;
    padding: 0px 50px 0px 0px;
}

.divcoloreach {
    position: relative;
    width: 30px;
    height: 30px;
    background: #ffffff;
    border-radius: 50%;
    float: left;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 10px;
}

 .divcolorone {
    background-size: contain;
    position: relative;
    width: 22.5px;
    height: 22.5px;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    background-color: #FFFFFF;
}


@media (max-width: 350px) { 
    .divcolorchoose {
        padding: 0px 45px 0px 0px;
    }

    .divcoloreach {
        margin-left: 3px;
    }
}
