.icons-container4 {
    position: absolute;
    width: -moz-fit-content;
    width: 100%;
    height: 1rem;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 50%, rgba(0, 0, 0, 0) 100%);
    display: flex;
    justify-content: center;
}
.icon-each4 {
    position: relative;
    width: 0.8rem;
    height: 0.8rem;
    background: #ffffff;
    border-radius: 50%;
    float: left;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 0.2rem;
}
.icon-icon4 {
    position: relative;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    background-color: #ffffff;
    background-size: contain;
}



@media only  screen and (min-width:425px) {
    .icons-container4 {
        height: 0.8rem;
    }
    .icon-each4 {
        width: 0.6rem;
        height: 0.6rem;
    }
    .icon-icon4 {
        width: 0.4rem;
        height: 0.4rem;
    }
}







