.divbackground3 {
    width: 100%;
    height: 100%;
    background: #5d5d5c;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    z-index: 101;
}

.divQRcodeContainer3 {
    width: 250px;
    height: 250px;
    background: #ffffff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
    border-radius: 5px;
    z-index: 101;
}

.imgclose3{
    width: 22px;
    height: 22px;
    position: absolute;
    top: 3%;
    right: 3%;
}


.imgQRcode3 {
    WIDTH: 150PX;
    HEIGHT: 150PX;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
}

.divcompanyandcontact3 {
    WIDTH: 175.5px;
    position: absolute;
    top: 75%;
    left: 10%;
    color: #333333;
}

.divcompanyandcontact3 div{
    WIDTH: 200px;
    font-size: 12px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #333333;
    line-height: 15px;
    margin: 7px;
    word-break:break-all
}

.divcompanyandcontact3 div img{ 
    width:12px;
    height: 12px;
    margin-right: 5px;
    vertical-align:middle;

}
