body{-webkit-tap-highlight-color:transparent;}
/* div */
.fix-service{ position:fixed; right:15px; bottom:15px; width:44px; height:fit-content; z-index:100;}
	/* ul */
	.fix-service .servise-list{ width:44px; height:auto;margin:0;padding:0;}
	/* li */
	.servise-list .fix-item{ position:relative; width:44px; height:44px; border-radius:50%;  margin-bottom:12px;list-style:none;border-radius: 50%;
		background-color: rgba(35, 36, 38, 0.6);}
		/* span */
		.fix-item .icon{ display:table-cell; text-align:center; vertical-align:middle; width:44px; height:44px	;}
			.icon img{ width:30px; height:30px;cursor: pointer; text-align:center; vertical-align:middle;}



