.divbackground {
    width: 100%;
    height: 100%;
    background: #5d5d5c;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    z-index: 101;
}

.divQRcodeContainer2 {
    width: 500px;
    height: 500px;
    background: #ffffff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
    border-radius: 5px;
    z-index: 101;
}

.imgclose{
    width: 24px;
    height: 24px;
    position: absolute;
    top: 3%;
    right: 3%;
}


.imgQRcode {
    WIDTH: 300PX;   
    HEIGHT: 300PX;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
}

.divcompanyandcontact {
    WIDTH: 350px;
    position: absolute;
    top: 75%;
    left: 10%;
    color: #333333;
}

.divcompanyandcontact div{
    WIDTH: 400px;
    font-size: 24px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 600;
    color: #333333;
    line-height: 30px;
    margin: 14px;
    word-break:break-all
}

.divcompanyandcontact div img{ 
    width:24px;
    height: 24px;
    margin-right: 10px;
    vertical-align:middle;

}
