.icon-containerparent3 {
    position: absolute;
    /* top: 0%; */
    /* top: 0; */
    width: 100%;
    height: 70px;
    background: rgba(0, 0, 0, 0.7);
    /* opacity: 0.4; */
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s,-webkit-transform .3s;
}


.icon-container3 {
    position: absolute;
    width: 100%;
    height: 70px;
    margin: auto;
    overflow:hidden;
    overflow-x: auto;
    display: flex;
    justify-content: space-between;
}
.icon-container3::before,
.icon-container3::after{
    content: '';
    display: block;
}

.icon-container3::-webkit-scrollbar {
    /* width: .01rem;
    height: 0.5rem; */
    display:none;
}
/* .icon-container3::-webkit-scrollbar-thumb {
    background: #666666;
    border-radius: 15px;
    margin:0 0 0.2rem 0
} */

.icon-each3 {
    position: relative;
    width: 34px;
    height: 34px;
    /* background: #ffffff; */
    /* border-radius: 50%; */
    float: left;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 14.5px 0 14.5px;
}

.icon-each-img3 {
    background-size: contain;
    position: relative; 
    width: 34px;
    height: 34px;
    /* border-radius: 50%; */
    top: 50%;
    transform: translateY(-50%);
    margin: auto;
    /* background-color: #FFFFFF; */
}

.LowerMenuOpenClose3 {
    width: 25px;
    height: 25px;
    padding: 0;
    border: none;
    background-color: rgba(0, 0, 0, 0.7);
    overflow: hidden;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    pointer-events: all;
    position: absolute;
    left: 9px;
    top: -25px;
    border-radius: 0;   
    background-size: contain;
}





@media (max-width: 350px) { 


    .icon-each3 {
        width: 28px;
        height: 28px;
    }
    
    .icon-each-img3 {
        width: 28px;
        height: 28px;
    }

    .LowerMenuOpenClose3 {
        width: 20px;
        height: 20px;
        top: -20px;
    }


}
