
.mask-layer {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .9);
    z-index: 99999;
}

.video-box {
    position: relative;
    top:-5%;    
    width: 100%;
    outline: none;
}

.video-box>div>video {
    /* object-fit: cover; */
    outline: none;
}

.close-box {
    position:fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top:25px;
    left:25px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba(35, 36, 38, 0.6);
    border: 2px solid rgba(255, 255, 255, 0.2);
}

.close-box>img {
    position: absolute;
    width: 25px;
    height: 25px;
    cursor: pointer;
}


@media only  screen and (min-width:600px) {
    .video-box {
        position: relative;
        top:-10%;
        width: 80%;
        height: fit-content;
    }
}


@media only  screen and (min-width:800px) {
    .video-box {
        position: relative;
        top:-10%;
        width: 60%;
        height: fit-content;
    }
}
