body{-webkit-tap-highlight-color:transparent;}
/* div */
.fix-service{ position:fixed; right:15px; bottom:15px; width:40px; height:fit-content; z-index:100;}
	/* ul */
	.fix-service .servise-list{ width:40px; height:auto;margin:0;padding:0;}
	/* li */
	.servise-list .fix-item{ position:relative; width:40px; height:40px; border-radius:50%;  margin-bottom:12px;list-style:none;}
		/* span */
		.fix-item .icon{ display:table-cell; text-align:center; vertical-align:middle; width:40px; height:40px;}
			.icon img{ width:40px; height:auto;cursor: pointer;}



